<template lang="jade">
  .hello
    h1.ui.header {{msg}} {{user && user.providerData[0].displayName}}
    h4.ui.header(v-if = "hands.length") 和 {{ hands.length }} 位朋友相互認識吧
    a.ui.massive.blue.button(@click="login", v-if="!user && !isFacebookApp()")
      i.facebook.icon
      | 登入
    a.ui.massive.red.button(v-else href="http://we.alearn.org.tw", target="_blank")
      | 請使用原生瀏覽器開啟本頁
    router-link.ui.massive.blue.button(to="/myFlag", v-else)
      img(:src = "'http://graph.facebook.com/' + id + '/picture'", alt="^_^")
      | 我的旗幟
    .ui.divider
    h2(v-if="hands.length") 最近更新 
    .ui.two.doubling.cards.container
      router-link.ui.card(v-for="(h, index) in searchBy(hands, mySearch).slice(0, 4)", :key="index", :to="'/flag/'+h.id")
        card(:h="h", :full="false", :mySearch="mySearch")

</template>

<script>

import { handsRef } from '../firebase'
import mix from '../mixins/mix.js'
import Card from './Card'

export default {
  name: 'hello',
  components: { Card },
  props: ['id', 'user', 'mySearch'],
  mixins: [mix],
  data () {
    return {
      msg: '歡迎'
    }
  },
  firebase: {
    hands: handsRef
  },
  methods: {
    login: function () {
      this.$emit('login')
    },
    isFacebookApp: function () {
      var ua = navigator.userAgent || navigator.vendor || window.opera
      return (ua.indexOf('FBAN') > -1) || (ua.indexOf('FBAV') > -1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495E;
}
</style>



